import React from 'react'

import {View, Panel, PanelHeader, ScreenSpinner, Group, List, Cell, platform, IOS, HeaderButton} from '@vkontakte/vkui'

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

class ReportDraggingList extends React.Component {
    constructor() {
        super()
        this.state = {
            activePanel: 'main',
            name: 'hello',
            popout: <ScreenSpinner />,
            report: {},
            items: []
        }
    }

    componentDidMount() {
        fetch(`${sessionStorage.getItem('address')}/api/v0/report/${this.props.reportID}/getdata`, {
            method: 'GET',
            headers: {
                'tehnarenok-token': sessionStorage.getItem('token')
            }
        }).then(data => {
            return data.json()
        }).then(data => {
            if(data.error) {
                this.props.go('error')
                return
            }
            this.setState({report: data.report})
        })
    }

    render() {
        return(
            //<View id={this.props.id} activePanel={this.state.activePanel} popout={this.state.popout}>
                <Panel id={this.props.id}>
                    <PanelHeader
                        left={<HeaderButton onClick={() => this.props.go('reports')}>{platform() === IOS ? <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>}
                    >
                        {this.state.report.name}
                    </PanelHeader>
                    <Group title='Описание'>
                        <p>
                            Для отчета необходимо загрузить файлы с кодом.
                        </p>
                        <p>
                            Текст в отчете можно добавить кликнув на значок +
                        </p>
                    </Group>
                    <Group title='Содержание отчета'>
                        <List>
                            <Cell key='0'>Титульный лист</Cell>
                            {this.state.items && this.state.items.length > 0 && this.state.items.map((value, index) => {
                                return (
                                    <Cell 
                                        key={index+1}
                                        draggable
                                    >{value.type}</Cell>
                                )
                            })}
                        </List>
                    </Group>
                </Panel>
            //</View>
        )
    }
}

export default ReportDraggingList